.err{
    color: green;
    font-size: 12pt;
    
}
.icon {
  
    font-size: 20pt;
   
}

.icon a:hover{
    
    padding-top: 0px;
    display: block;
    position: absolute;
    padding-left: 7px;
    padding-right: 7px;
    padding-top: 1px;
    border-radius: 25px;
    height: 35px;
    width: 35px;
    margin-top: 0px;
    margin-top: 0px;
    margin-left: 0px;
    transition: 0.2s;

}
.icon a{
    background-color: rgba(191, 196, 212, 0.5);
    padding-top: 0px;
    display: block;
    position: absolute;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 1px;
    border-radius: 25px;
    height: 0px;
    width: 0px;
    margin-top: 15px;
    margin-left: 15px;
    
}
.icon  svg {
  
    font-size: 15pt;
    margin-top: -47px;
    margin-left: -7px;
   
}
.icon a:hover svg {
  
    font-size: 15pt;
    margin-top: -16px;
    margin-left: 1px;
    transition: 0.2s;
}
.delete{
    position: absolute; 
    border-radius: 50px;
}
.delete:hover{
   padding-top: 5px;
   padding-left: 5px;
   padding-right: 5px;
   padding-bottom: 5px;
    background-color: rgb(218, 198, 198);
   
    transition: 0.2s;
    margin-top: -5px;
    margin-left: -5px;
}
